.headerContainer{
    background-color: #131A22;
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 100;
    top: 0;
}

.headerDropDownText{
    font-size: 12px;
}

.navbar-toggler-icon{
    font-size: 20px;
    color: white;
}

.hamburgerButton{
    padding: 5px 4px;
    border: 1px thin white;
}

.headerLogo{
    margin: 11px 20px 0px 5px;
    object-fit: contain;
    width: 100px;
}

.headerSearch{
    flex: 1;
}   

.headerSearchInput{
    padding: 21px;
}

input{
    height: 12px;
    padding: 10px;
    width: 100%;
}

.headerSearchIconContainer{
    background-color: #febd69;
}
.headerSearchIcon{
    font-size: 30px;
}

.headerOptions{
    margin-left: 12px;
    margin-right: 12px;
}

.dropDownBtn{
    width: 21px;
    height: 20px;
    vertical-align: top;
}

.headerNav{
    justify-content: space-evenly;
}

.headerOptionLineOne{
    font-size: 12px;
}

.headerOptionLineTwo{
    font-size: 14px;
    font-weight: 700;
}

.navbarShoppingCount{
    color: #f08804;
    display: flex;
    font-weight: 700;
}

.shoppingCartText{
    vertical-align: bottom;
}
